import { Component, Input, OnInit, Output } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { OpportunitiesService } from '../services/opportunities.service';
import { db } from '../../db';
@Component({
  selector: 'app-opportunities',
  templateUrl: './opportunities.component.html',
  styleUrls: ['./opportunities.component.css'],
})
export class OpportunitiesComponent implements OnInit {
  opportunities = db.opportunities;
  displayOpportunity = false;
  opps: any = [];
  opportunity: any;

  constructor(
    private http: HttpClient,
    private _opportunitiesService: OpportunitiesService
  ) {}

  ngOnInit(): void {
    this._opportunitiesService._opportunitySource.subscribe(opp => this.opportunity = opp);
    this.opportunities.forEach((op) =>
      this.http
        .get(`https://torre.co/api/opportunities/${op}`)
        .subscribe((job) => {
          this.opps.push(job);
        })
    );
  }


  sendOpportunity(opp: {}) {
    this._opportunitiesService.sendOpportunityInfo(opp);
  }

}
