import { Component, OnInit } from '@angular/core';
import { db } from '../db';
import { OpportunitiesService } from './services/opportunities.service';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
})
export class AppComponent {
  displayPeople = true;
  displayOpportunities = false;
  title = 'test-torre';
  opportunities = db.opportunities;
  opportunity: any;

  constructor(private _opportunitiesService: OpportunitiesService) {}

  ngOnInit(): void {
    this._opportunitiesService._opportunitySource.subscribe(
      (opp) => (this.opportunity = opp)
    );
  }

  showOpportunities() {
    this.displayOpportunities = true;
    this.displayPeople = false;
    this._opportunitiesService.sendOpportunityInfo({});
  }

  showPeople() {
    this.displayOpportunities = false;
    this.displayPeople = true;
    this._opportunitiesService.sendOpportunityInfo({});
  }
}
